.all-orders {
    width: 100%;
    min-height: 100vh;
    padding: 0 12px;


    // &__delete {
    //     display: flex;
    //     column-gap: 8px;
    //     justify-content: center;
    //     align-items: center;
    //     margin: 24px 0;

    //     &-btn {
    //         font-size: 20px;
    //         border-radius: 4px;
    //         border: 1px solid red;
    //         color: white;
    //         background-color: red;
    //     }
    // }

    &__title {
        font-size: 32px;
        text-align: center;
    }

    &__table {
        // border: 1px solid blue;
        font-size: 14px;
        overflow-x: scroll;
        width: 100%;
        border-collapse: collapse;
        margin: 24px 0;

        & th {
            padding: 8px 0;
            background-color: rgba(255,255,255,0.5);
            color: #333;

            &:nth-child(1) {
                width: 40px;
                // border: 1px solid gold;
                text-align: center;
            }
        }

        &-row {
            border-bottom: 1px solid grey;
            transition: 0.3s;
            &:nth-child(2n) {
                background-color: rgba(255,255,255,0.12);
            }

            &:hover {
                background-color: rgba(255,255,255,0.8);
                color: rgba(0,0,0,0.8);
            }
        }

        &-cell {
            padding: 12px 4px;
            justify-content: center;
            align-items: center;

            &:nth-child(1) {
                width: 40px;
                // border: 1px solid gold;
                text-align: center;
            }
        }
    }
}

.controlls {
    width: 100%;

    &__box {
        border: 1px solid white;
        min-height: 40px;
        margin: 10px 0;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        text-align: center;

        span {
            font-size: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__btn {
        // padding: 8px;
        font-size: 20px;
    }
}

.order-info {
    // border: 2px solid red;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__table {
        width: 100%;
        border: 2px solid white;

        &-row {
            // color: gold;

            td {
                padding: 4px 8px;
            }

            &:nth-child(2n) {
                background-color: rgba(255,255,255,0.12);
            }
        }
    }
}